@import '../../node_modules/swiper/dist/css/swiper.min.css';
:root {
  --font-main: "Hind", system-ui;
  --font-accent: "b", system-ui;
  --color-main-bg: #fff;
  --color-accent-bg: #fafafa;
  --color-title-text: #333;
  --color-accent-text: #fff;
  --color-main-text: #a6a7aa;
  --color-theme: #17bed2;
  --max-width: 1200px;
}

@custom-media --viewport-laptop-l (width <= 1440px);
@custom-media --viewport-laptop (width <= 1024px);
@custom-media --viewport-tablet (width <= 768px);
@custom-media --viewport-mobile-l (width <= 425px);
@custom-media --viewport-mobile-m (width <= 375px);
@custom-media --viewport-mobile-s (width <= 320px);
:root {
  --vh: 5.68px;
}

.home {
  font-family: var(--font-main);
}
.home__img {
  border: 1px dashed;
}
.home__section {
  position: relative;
  background-color: var(--color-main-bg);
  z-index: 3;
}
.home__section--preview {
  min-height: 95vh;
  background-color: transparent;
}
@supports (--css: variables) {
  .home__section--preview {
    min-height: calc(var(--vh) * 95);
  }
}
.home__section--education, .home__section--contacts {
  background-color: var(--color-accent-bg);
}
.home__section--slider {
  width: 100%;
  height: calc(100vh - 6.8em);
}
@supports (--css: variables) {
  .home__section--slider {
    min-height: calc(var(--vh) * 85);
  }
}
.home__section--footer .home__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home .home__section--footer .home__wrap .text {
  margin-bottom: 0;
}
.home__wrap {
  position: relative;
  display: flex;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 3em 3em;
  z-index: 2;
}
.home__wrap--preview {
  min-height: 95vh;
  flex-direction: column;
  justify-content: center;
  padding-top: 5.8em;
}
@supports (--css: variables) {
  .home__wrap--preview {
    min-height: calc(var(--vh) * 95);
  }
}
@media (--viewport-tablet) {
  .home__wrap {
    flex-direction: column;
    padding-left: 1em;
    padding-right: 1em;
  }
  .home__section--footer .home__wrap {
    flex-direction: row;
  }
  .home__section--footer .home__wrap .logo {
    margin-right: 2em;
  }
}
.home__bg {
  position: fixed;
  top: 0;
  left: 0;
  background-image: url("../assets/img/01.png");
  background-size: cover;
  background-position: center;
  min-height: 95vh;
  width: 100vw;
}
@supports (--css: variables) {
  .home__bg {
    min-height: calc(var(--vh) * 95);
  }
}
.home__title {
  position: relative;
  font-size: 5em;
  line-height: 1.2;
  font-weight: bold;
  color: var(--color-accent-text);
  margin-bottom: 0.2em;
}
.home__subtitle {
  position: relative;
  position: relative;
  font-size: 1.5em;
  color: var(--color-accent-text);
  margin-bottom: 3em;
}
.home__subtitle:after {
  content: "";
  display: block;
  position: absolute;
  bottom: -1.5em;
  left: 0;
  height: 1px;
  width: 50%;
  background-color: var(--color-main-bg);
  opacity: 0.5;
}
.home__link-list {
  position: relative;
  display: flex;
}
.home__link {
  display: block;
  width: 3em;
  height: 3em;
  border-radius: 100%;
  margin-right: 1em;
  transition: background 0.3s ease;
}
.home__link span {
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home__link svg {
  position: relative;
  width: 50%;
  height: 50%;
  fill: var(--color-accent-text);
}
.home__link:hover {
  background-color: var(--color-main-bg);
}
.home__link:hover svg {
  fill: var(--color-title-text);
}
.home__title-box {
  width: 35%;
  text-align: right;
  padding: 1em;
}
@media (--viewport-tablet) {
  .home__title-box {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    text-align: left;
  }
}
.home .h2 {
  font-size: 2.5em;
  font-weight: bold;
  color: var(--color-title-text);
}
.home .h2--uppercase {
  text-transform: uppercase;
}
.home .h2--accent {
  color: var(--color-accent-text);
}
.home .h3 {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 1em;
  color: var(--color-title-text);
}
.home .motto {
  color: var(--color-main-text);
}
.home__content {
  width: 65%;
  color: var(--color-main-text);
  padding: 2em 1em 1em 4em;
}
@media (--viewport-tablet) {
  .home__content {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
}
.home .text {
  text-decoration: none;
  color: var(--color-main-text);
  margin-bottom: 3em;
}
.home .text p {
  margin-bottom: 1em;
}
.home .text--bold {
  font-weight: bold;
}
.home .text--accent {
  color: var(--color-accent-text);
}
.home .text a {
  color: var(--color-theme);
  opacity: 0.8;
}
.home .text a:hover {
  opacity: 1;
}
.home__item {
  display: flex;
  flex-wrap: wrap;
  color: var(--color-title-text);
  justify-content: space-between;
}
.home__item .text {
  margin-bottom: 1em;
}
.home__progress-line {
  position: relative;
  width: 100%;
  margin-bottom: 2em;
}
.home__progress-line span {
  display: block;
  height: 3px;
  background-color: var(--color-main-text);
  width: 100%;
}
.home__progress-line span:first-child {
  opacity: 0.25;
}
.home__progress-line span:last-child {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-theme);
  transform-origin: 0;
  transform: scaleX(0);
}
.home__progress-line.show span:last-child {
  transform: scaleX(1);
  transition: transform 1s cubic-bezier(0.39, 0.575, 0.565, 1) 0.2s;
}
.home__cards {
  display: flex;
  flex-wrap: wrap;
}
.home__card {
  width: 50%;
  min-height: 10em;
  text-decoration: none;
  padding: 2em;
  background-color: var(--color-main-bg);
}
.home__card .text,
.home__card .h3 {
  transition: transform 0.3s ease;
}
.home__card .text {
  margin-bottom: 1em;
}
.home__card:nth-child(4n+2), .home__card:nth-child(4n+3) {
  background-color: var(--color-theme);
}
.home__card:nth-child(4n+2) .text,
.home__card:nth-child(4n+2) .h3, .home__card:nth-child(4n+3) .text,
.home__card:nth-child(4n+3) .h3 {
  color: var(--color-accent-text);
}
.home__card:nth-child(4n+2) svg, .home__card:nth-child(4n+3) svg {
  fill: var(--color-accent-text);
}
.home__card:hover .text,
.home__card:hover .h3,
.home__card:hover .card__icon {
  transform: translateY(-30px);
}
.home__card:hover .card__icon {
  opacity: 0;
}
@media (--viewport-tablet) {
  .home__card {
    width: 100%;
  }
  .home__card:nth-child(2n+2) {
    background-color: var(--color-theme);
  }
  .home__card:nth-child(2n+2) .text,
.home__card:nth-child(2n+2) .h3 {
    color: var(--color-accent-text);
  }
  .home__card:nth-child(2n+2) svg {
    fill: var(--color-accent-text);
  }
  .home__card:nth-child(4n+3) {
    background-color: var(--color-main-bg);
  }
  .home__card:nth-child(4n+3) .text,
.home__card:nth-child(4n+3) .h3 {
    color: var(--color-title-text);
  }
  .home__card:nth-child(4n+3) svg {
    fill: var(--color-title-text);
  }
}
.home .card__icon {
  margin-bottom: 1em;
  display: block;
  width: 2em;
  height: 2em;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.home .card__icon svg {
  width: 100%;
  height: 100%;
}
.home__history {
  display: flex;
  flex-direction: column;
}
.home .history__point {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 50%;
  text-align: right;
  padding: 2em;
}
@media (--viewport-tablet) {
  .home .history__point {
    padding: 1em 1em 1em 0;
  }
  .home .history__point:nth-child(2n+2) {
    padding: 1em 0 1em 1em;
  }
}
.home .history__point:before {
  content: "";
  position: absolute;
  top: 3.5em;
  right: -1px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 1em 1em 1em 0;
  border-color: transparent var(--color-theme) transparent transparent;
}
.home .history__point:after {
  content: "";
  position: absolute;
  top: 0;
  right: -1px;
  height: 100%;
  width: 2px;
  background-color: var(--color-main-text);
}
.home .history__point:nth-child(2n) {
  align-self: flex-end;
  text-align: left;
}
.home .history__point:nth-child(2n):after {
  left: -1px;
  right: auto;
}
.home .history__point:nth-child(2n):before {
  left: -1px;
  right: auto;
  border-width: 1em 0 1em 1em;
  border-color: transparent transparent transparent var(--color-theme);
}
.home .history__point .text,
.home .history__point .h3 {
  margin-bottom: 0;
}
.home .swiper-pagination-bullet-active {
  background-color: var(--color-theme);
}
.home__list {
  display: flex;
  flex-wrap: wrap;
}
.home__list--column {
  flex-direction: column;
}
@media (--viewport-tablet) {
  .home__list--mobile-column {
    flex-direction: column;
  }
}
.home__box {
  flex-grow: 1;
  flex-shrink: 0;
  padding: 0 1em;
}
.home__box .text {
  margin-bottom: 1em;
}
@media (--viewport-tablet) {
  .home__box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1em;
  }
}
.home__btn-top {
  position: fixed;
  bottom: 2em;
  right: 2em;
  width: 3em;
  height: 3em;
  border-radius: 0.5em;
  background-color: var(--color-main-text);
  color: var(--color-accent-text);
  text-transform: uppercase;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
}
.home .logo svg path:first-child {
  fill: var(--color-main-text);
}
.home .home__section--about .text--show, .home__card,
.home .history__point .text,
.home .history__point .h3, .home__box {
  opacity: 0;
  transform: translateY(50px);
}
.home .home__section--about .text--show.show, .home__card.show,
.home .history__point .text.show,
.home .history__point .h3.show, .home__box.show {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 1s ease 0.2s, transform 1s cubic-bezier(0.39, 0.575, 0.565, 1) 0.2s;
}
[data-state=scrolling] .home .home__btn-top {
  opacity: 0.5;
  pointer-events: all;
}
@media (--viewport-tablet) {
  .home .swiper-slide {
    display: flex;
    flex-direction: column;
  }
}
.home .slide__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (--viewport-tablet) {
  .home .slide__bg {
    position: relative;
    height: 50%;
  }
}
.home .slide__bg img {
  object-fit: cover;
  min-width: 100%;
  height: 100%;
}
@media (--viewport-tablet) {
  .home .slide__bg img {
    height: 100%;
  }
}
.home .slide__wrap {
  position: relative;
  width: 50%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: flex-end;
}
@media (--viewport-tablet) {
  .home .slide__wrap {
    width: 100%;
    flex-grow: 1;
  }
}
.home .slide__group {
  max-width: calc(var(--max-width) / 2);
  width: 100%;
  font-size: 1.1em;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 3em;
  padding-right: 3em;
  box-sizing: border-box;
}
@media (--viewport-tablet) {
  .home .slide__group {
    font-size: 1em;
    padding-top: 2em;
    justify-content: flex-start;
  }
}
.home .slide__group .h2 {
  position: relative;
}
.home .slide__group .h2:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -0.7em;
  display: block;
  width: 0.5em;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.home .slide__group:hover .h2:before {
  opacity: 1;
}
.home .slide__group .text {
  width: 80%;
}